import React, { useRef, useState, useEffect } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import SwiperCore, { Autoplay, Navigation } from "swiper";


import hungry from './img/works/hungry.png'
import cloud from './img/works/cloud_bread.png'
import grocery from './img/works/grocery.png'
import social from './img/works/social.png'
import ukraine from './img/works/ukraine.png'
import ukrainians from './img/works/ukrainians.png'
import placeholder from './img/works/place.png'

import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init();

SwiperCore.use([Navigation]);

let works = [{name:'hungry people', logo: hungry, link:'', react:false, sass: true, js:false, html:true}, 
    {name:'cloud bread', logo:cloud, link:'', react:true, sass: true, js:false, html:false}, 
    {name:'grocery', logo:grocery, link:'', react:false, sass: true, js:false, html:true}, 
    {name:'social mission', logo:social, link:'https://sofo4ka19.github.io/my-social-mission/', react:false, sass: false, js:true, html:true},
    {name:'ukraine', logo:ukraine, link:'', react:false, sass: true, js:false, html:true},
    // {name:'sh2', logo:placeholder, link:'', react:true, sass: true, js:true, html:true},
    // {name:'sh3', logo:placeholder, link:'', react:true, sass: true, js:true, html:true},
    {name:'ukrainians', logo:ukrainians, link:'', react:true, sass: true, js:false, html:false},
    {name:'sjh1', logo:placeholder, link:'', react:true, sass: true, js:true, html:true},
    {name:'sjh2', logo:placeholder, link:'', react:true, sass: true, js:true, html:true},
    {name:'sjh3', logo:placeholder, link:'', react:true, sass: true, js:true, html:true}];

let arrProject = [];

function MyWorks(props){
    useEffect(() => {
        AOS.init({
            once: true,
        });
        AOS.refresh();
    }, []);
    const navigationPrevRef = React.useRef(null)
    const navigationNextRef = React.useRef(null)
    const [numberOfSlides, setNumberOfSlides] = useState(3)
    const [select, setSelect] = useState(false)
    useEffect(() => {
        if(window.innerWidth>=1000){
            setNumberOfSlides(3)
        }
        else if(window.innerWidth>=700){
            setNumberOfSlides(2)
        }
        else{
            setNumberOfSlides(1)
        }
        (window.innerWidth>=800) ? setSelect(false) : setSelect(true)
        console.log(window.innerWidth)
    }, [window.innerWidth])
    const[arr, setArr]=useState([{name: 'cloud bread', logo: cloud, link: ''}, {name: 'ukrainians', logo: ukrainians, link: ''}, {name: 'sjh1', logo: placeholder, link: ''}, {name: 'sjh2', logo: placeholder, link: ''}, {name: 'sjh3', logo: placeholder, link: ''}])
    function changeCategory(event){
        for(let i = 0; i < event.currentTarget.children.length; i++) {
            if(event.currentTarget.children[i].classList.contains("category__item_active")) {
                event.currentTarget.children[i].classList.remove("category__item_active")
                event.target.classList.add("category__item_active")
            }
        }
        arrProject = [];
            if(event.target.textContent === 'react') {
                for(let j = 0; j < works.length; j++) {
                    if(works[j].react) {
                        arrProject.push({name: works[j].name, logo: works[j].logo, link: works[j].link})
                    }
                }
                console.log("react: " + arrProject)
            }
            else if (event.target.textContent === 'sass') {
                for(let j = 0; j < works.length; j++) {
                    if(works[j].sass) {
                        arrProject.push({name: works[j].name, logo: works[j].logo, link: works[j].link})
                    }
                }
                console.log("sass: " + arrProject)
            }
            else if (event.target.textContent === 'js') {
                for(let j = 0; j < works.length; j++) {
                    if(works[j].js) {
                        arrProject.push({name: works[j].name, logo: works[j].logo, link: works[j].link})
                    }
                }
                console.log("js: " + arrProject)
            }
            else if (event.target.textContent === 'html/css') {
                for(let j = 0; j < works.length; j++) {
                    if(works[j].html) {
                        arrProject.push({name: works[j].name, logo: works[j].logo, link: works[j].link}) 
                    }
                }
                console.log("html/css: " + arrProject)
            }
            // else {
            //     alert("Sorry, something wrong :c")
            // }
            setArr(arrProject)
    }
    function changeSliderTheme(){
        return arr.map(item => {
            return(
                <SwiperSlide style={{backgroundImage: `url(${item.logo})`}} key={item.name}>
                    {item.link!='' &&
                    <div className="swiper__blur"><button className="button swiper__button"><a target="_blank" className="button__text" href={item.link}>View website</a></button></div>
                    }
                </SwiperSlide>
            )
        })
    }
    return(
        <section ref={props.reference} id="portfolio" className="section item" data-id="portfolio">
                <p data-aos='fade-up' className="headline">my works</p>
                <div data-aos='zoom-in' className='section__content' style={{position: 'relative'}}>
                    {select ? 
                    <select className='category' onClick={changeCategory}>
                        <option className='category__item category__item_active'>REACT</option>
                        <option className='category__item'>SASS</option>
                        <option className='category__item'>JS</option>
                        <option className='category__item'>HTML/CSS</option>
                    </select> :
                    <ul className='category' onClick={changeCategory}>
                        <li className='category__item category__item_active'>react</li>
                        <li className='category__item'>sass</li>
                        <li className='category__item'>js</li>
                        <li className='category__item'>html/css</li>
                    </ul>
                    }
                    
                    <div className="prev" ref={navigationPrevRef} />
                    <div className="next" ref={navigationNextRef} />
                    <Swiper
                    slidesPerView={numberOfSlides}
                    spaceBetween={30}
                    slidesPerGroup={1}
                    loopFillGroupWithBlank={true}
                    autoplay={{
                      delay: 2500,
                      disableOnInteraction: false,
                    }}
                    pagination={{
                      clickable: true,
                    }}
                    navigation={{
                        prevEl: navigationPrevRef.current,
                        nextEl: navigationNextRef.current,
                    }}
                    onBeforeInit={(swiper) => {
                        swiper.params.navigation.prevEl = navigationPrevRef.current;
                        swiper.params.navigation.nextEl = navigationNextRef.current;
                    }}
                    loop={true}
                    modules={[Autoplay, Navigation]}
                    className="mySwiper" id="swiperWork"
                >{changeSliderTheme()}</Swiper>
                </div>
        </section>
    )
}

export default MyWorks