import React, {useEffect, useRef, useState} from "react";
// import './Nav.scss';

function Nav(props){
    const navigation = useRef(null)
    const burger = useRef(null);
    const [active, setActive] = useState('nav__list')
    // const [scrolling, setScrolling] = useState('##')
    
    function navToggle() {
        
        if(active === 'nav__list'){
            // if(window.scrollY<window.innerHeight){
            if(window.scrollY <= props.scrolling){
                navigation.current.scrollIntoView()
            }
            // }
            setActive('nav__list nav_active')
            document.body.style.overflow='hidden'
        }
        else{
            setActive('nav__list')
            document.body.style.overflow='auto'
        }
        // (active === 'nav__list') ? setActive('nav__list nav_active') : setActive('nav__list');
        // (icon === 'nav__toggler') ? setIcon('nav__toggler') : setIcon('nav__toggler');
        console.log(active)
        
    }

    return(
        <nav className="nav" ref={navigation}>
            <ul className={active} ref={props.reference}>
                <li className="nav__item nav__item_active">
                    <a onClick={navToggle} href="#header" className="nav__anchor">home</a>
                </li>
                <li className="nav__item">
                    <a onClick={navToggle} href="#about" className="nav__anchor">about me</a>
                </li>
                <li className="nav__item">
                    <a onClick={navToggle} href="#hard skills" className="nav__anchor">hard skills</a>
                </li>
                <li className="nav__item">
                    <a onClick={navToggle} href="#portfolio" className="nav__anchor">portfolio</a>
                </li>
                <li className="nav__item">
                    <a onClick={navToggle} href="#contact" className="nav__anchor">contact</a>
                </li>
            </ul>
            <div ref={burger} id='hideBurger' onClick={navToggle} className='nav__toggler'><i className="fas fa-bars"></i></div>
        </nav>
    )
}

export default Nav