import React, {useState, useRef, useEffect} from 'react'
import Header from './Header';
import Nav from './Nav';
import AboutMe from './AboutMe';
import HardSkills from './HardSkills';
import MyWorks from './MyWorks';
import Footer from './Footer';

function App(){
    const header = useRef(null)
    const list = useRef(null)
  const about = useRef(null)
  const skills = useRef(null)
  const works = useRef(null)
  const footer = useRef(null)
  
  const [offset, setOffset] = useState(0);
  
  const [headerTop, setHeaderTop] = useState(0);
  const [headerHeight, setHeaderHeight] = useState(0)
  
  const [aboutTop, setAboutTop] = useState(0);
  const [aboutHeight, setAboutHeight] = useState(0)
  
  const [skillsTop, setSkillsTop] = useState(0);
  const [skillsHeight, setSkillsHeight] = useState(0)
 
  const [worksTop, setWorksTop] = useState(0);
  const [worksHeight, setWorksHeight] = useState(0)

  const [footerTop, setFooterTop] = useState(0);
  const [footerHeight, setFooterHeight] = useState(0)

  const [index, setIndex] = useState(0)

  useEffect(() => {
    const onScroll = () => setOffset(window.pageYOffset);
    // clean up code
    window.removeEventListener('scroll', onScroll);
    window.addEventListener('scroll', onScroll, { passive: true });
    return () => window.removeEventListener('scroll', onScroll);
  }, []);

  useEffect(() => {
    setHeaderTop(header.current.offsetTop)
    setHeaderHeight(header.current.offsetTop + 4*(header.current.offsetHeight/5))
    
    setAboutTop(about.current.offsetTop - 50 - header.current.offsetHeight/5)
    // console.log(mainTop + " " + mainHeight)
    setAboutHeight(about.current.offsetTop + 4*(about.current.offsetHeight/5))

    setSkillsTop(skills.current.offsetTop - about.current.offsetHeight/5)
    setSkillsHeight(skills.current.offsetTop + 4*(skills.current.offsetHeight/5))

    setWorksTop(works.current.offsetTop - skills.current.offsetHeight/5)
    setWorksHeight(works.current.offsetTop + 4*(works.current.offsetHeight/5))
    
    setFooterTop(footer.current.offsetTop - works.current.offsetHeight/5)
    setFooterHeight(footer.current.offsetTop + footer.current.offsetHeight)
    
    
    if(window.scrollY>=headerTop && window.scrollY<=headerHeight) {
      setIndex(0)
    }
    else if (window.scrollY>=aboutTop && window.scrollY<=aboutHeight) {
        setIndex(1)
    }
    else if (window.scrollY>=skillsTop && window.scrollY<=skillsHeight) {
        setIndex(2)
    }
    else if (window.scrollY>=worksTop && window.scrollY<=worksHeight) {
        setIndex(3)
    }
    else if (window.scrollY>=footerTop && window.scrollY<=footerHeight) {
        setIndex(4)
    }
    else {
      /**/ 
    }

    for(let i=0; i<list.current.children.length; i++){
        if(list.current.children[i].classList.contains("nav__item_active")) {
            list.current.children[i].classList.remove("nav__item_active")
        }
    }
    list.current.children[index].classList.add("nav__item_active")

    // console.log(list)
  }, [window.scrollY])
  return(
    <React.Fragment>
        <Header reference={header} />
        <Nav reference={list} scrolling={aboutTop} />
        <AboutMe reference={about} />
        <HardSkills reference={skills} />
        <MyWorks reference={works} />
        <Footer reference={footer} />
    </React.Fragment>
  )
}

export default App