import React, {useEffect} from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init();

function ProgressBar(props){
    useEffect(() => {
        AOS.init({
            once: true,
        });
        AOS.refresh();
    }, []);
    return(
        <div data-aos='fade-right' data-aos-duration='700' className="diagram__line">
            <p className="text diagram__text">{props.name}</p>
            <div className="diagram__fill" style={{width: `${props.percent*0.8}%`}}>{props.percent}%</div>
        </div>
    )
}

export default ProgressBar