import React, { useRef, useState } from "react";
import github from "./img/icons/github.svg";
import instagram from "./img/icons/insta.svg";
import linkedin from "./img/icons/linkedin.svg";
import emailjs from '@emailjs/browser';

function Footer(props){
    const form = useRef();
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [message, setMessage] = useState('')
    const sendEmail = (e) => {
        e.preventDefault();

        emailjs.sendForm('service_k1e116a', 'template_vhcvrwe', form.current, 'h2hR-WxAGTRMIUgPf')
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });
        setName('')
        setEmail('')
        setMessage('')
        alert('Your message was sent')
    };
    return(
        <footer ref={props.reference} id="contact" className="footer item" data-id="contact">
            <p className="headline footer__headline">contact</p>
            <form ref={form} onSubmit={sendEmail} className="form">
                <input onChange={event => setName(event.target.value)} value={name} name="user_name" className="form__item" type='text' placeholder="Name" required/>
                <input onChange={event => setEmail(event.target.value)} value={email} name="user_email" className="form__item" type='email' placeholder="Email" required/>
                <textarea onChange={event => setMessage(event.target.value)} value={message} name="message" className="form__item form__item_big" placeholder="Your message..." required></textarea>
                <div className="social-media">
                    <a target="_blank" href="https://github.com/sofo4ka19"><img src={github} className="social-media__item"/></a>
                    <a target="_blank" href="https://instagram.com/creative_artsof?igshid=YmMyMTA2M2Y="><img src={instagram} className="social-media__item"/></a>
                    <a target="_blank" href="https://www.linkedin.com/in/%D1%81%D0%BE%D1%84%D0%B8%D1%8F-%D0%B1%D1%80%D0%BE%D1%8F%D0%BA%D0%BE-582249243/"><img src={linkedin} className="social-media__item"/></a>
                </div>
                <button type="submit" value="Send" className="button footer__button">Send message</button>
            </form>
        </footer>
    )
}

export default Footer;