import React, {useEffect} from "react";
import ProgressBar from "./ProgressBar";
import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init();

function HardSkills(props){
    useEffect(() => {
        AOS.init({
            once: true,
        });
        AOS.refresh();
    }, []);
    return(
        <section ref={props.reference} id="hard skills" className="section item" data-id="hard skills">
                <p data-aos='fade-up' className="headline">Hard skills</p>
                <div className="section__content diagram text">
                        <ProgressBar name='HTML' percent='85' />
                        <ProgressBar name='CSS/SASS' percent='80' />
                        <ProgressBar name='JavaScript' percent='55' />
                        <ProgressBar name='React' percent='30' />
                        <ProgressBar name='UI/UX' percent='60' />
                </div>
        </section>
    )
}

export default HardSkills