import React from "react";
import * as THREE from "three";
import NET from 'vanta/dist/vanta.net.min';


class Header extends React.Component {
  constructor(props){
    super(props)
    this.vantaRef=React.createRef()
  }
  componentDidMount(){
    this.vantaEffect = NET({
      el: this.props.reference.current, THREE,
      backgroundAlpha: 0,
      color: 0xEE6C4D,
      backgroundColor: 0xBAD4FF,
      showDots: false,
      points: 12.00,
      spacing: 16.00,
      maxDistance: 24.00,
      mouseControls: false,
    })
  }
  componentWillUnmount(){
    if(this.vantaEffect) this.vantaEffect.destroy()
  }
  render(){
    return(
      <header id="header" className='header wrapper item' ref={this.props.reference} data-id="home">
        {/* <div className='wrapper header__wrapper'> */}
          <h1 className='header__headline'>Hi, I'm <span className='text_coral'>S</span>ofia <br/>Front-End developer</h1>
          <div className='button-cont'>
            <button className='button header__button'>
              <a href="#portfolio" className='button__text'>My works</a>
            </button>
            <button className='button header__button'>
              <a href="#contact" className='button__text'>Contact me</a>
            </button>
          </div>
        {/* </div> */}
      </header>
    )
  }
}

export default Header;
