import React, { useEffect } from "react";
import me from "./img/me.jpg"
import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init();

function AboutMe(props){
    useEffect(() => {
        AOS.init({
            once: true,
        });
        AOS.refresh();
    }, []);
    return(
        <section ref={props.reference} id="about" className="section item" data-id="about">
                <p data-aos='fade-up' className="headline">About me</p>
                <div className="section__content section__content_space-between">
                    <p data-aos='fade-right' className="text section__text">Hi! My name is Sofia Broiako. I’m 16 years old. I really enjoy creating beautiful web-sites. Also I’m interested in singing, playing a piano, painting, ice skating and studing something new. I practice web-design, but my main occupation is front-end web-developing. My way in this started in 2018. Then I decided to go to the computer programming school in my native city Kryvyi Rih, Ukraine. I wanted only to try something new, but later I understand that I really like it and enjoy this process. More about me you can read on this web-site. I hope that I will be able to your dream web-site!</p>
                    <img data-aos='zoom-in-left' src={me} className="section__img"/>
                </div>
        </section>
    )
}

export default AboutMe